@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bai+Jamjuree:wght@200;300;400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Syncopate:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bodoni+Moda:wght@400;500;600;700;800;900&display=swap");
:root {
  --color-primary: #d53f41;
  --color-dark: #626262;
  --color-darker: #464646;
  --color-light: #dbd8d6;
  --color-lighter: #f5f0ec;
}

* {
  margin: 0;
  padding: 0;
  border: none;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
}

html,
body {
  overflow-x: hidden;
}

html {
  font-size: 62.5%;
}
body {
  font-family: "Poppins", sans-serif;
  font-size: 2rem;
  font-weight: 400;
  line-height: 1.7;
  letter-spacing: 1px;
  background-color: var(--color-lighter);
  color: var(--color-darker);
}

.wrapper {
  margin: 0 5vw;
}

section {
  padding: 6vw 0;
}
.section-title {
  font-family: "Syncopate", sans-serif;
  font-size: 1.5rem;
  text-transform: lowercase;
  color: var(--color-primary);

  padding-bottom: 5vw;
}

.m-h-100vh {
  min-height: 100vh;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 2rem 0;
  font-family: "Syncopate", sans-serif;

  font-size: 1.5rem;
  text-transform: lowercase;
}

.links,
.blog-link {
  font-weight: 700;
}

.links a,
.blog-link a {
  color: var(--color-dark);
  position: relative;
}
.links a::after,
.blog-link a::after {
  content: "";
  width: 0%;
  height: 0.2rem;
  position: absolute;
  top: 50%;
  left: 50%;
  background-color: var(--color-primary);
  transform: translate(-50%, -50%);
  transition: 0.5s;
}
.links a:hover::after,
.blog-link a:hover::after {
  width: 120%;
}

.logo a {
  color: var(--color-darker);
}

.hero {
  font-family: "Bai Jamjuree", sans-serif;
  text-transform: uppercase;
  text-align: center;
  font-size: 10vw;
  line-height: 1.2;
  min-height: 100vh;
  color: var(--color-darker);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.ethereal {
  position: relative;
}

.canvas {
  position: relative;
}

.ethereal span,
.canvas span {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: var(--color-lighter);
}
.photos {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: repeat(5, 1fr);
}

.photo {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  overflow: hidden;
}

.one {
  grid-column: 2;
  grid-row: 5;
}

.two {
  grid-column: 7;
  grid-row: 4;
}

.three {
  grid-column: 5;
  grid-row: 1;
}
.four {
  grid-column: 4;
  grid-row: 3;
}

.five {
  grid-column: 1;
  grid-row: 2;
}

.features {
  display: grid;
  grid-template-columns: 30% auto;
  align-items: center;
  gap: 10rem;
}

.features-title {
  text-transform: uppercase;
  letter-spacing: 5px;
  font-weight: 500;
}

.features img {
  width: 100%;
}

.features-left,
.features-right {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  position: relative;
}

.feature-shutter-left,
.feature-shutter-right {
  position: absolute;
  z-index: 1;
  height: 100%;
  width: 100%;
  background-color: var(--color-lighter);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.about p {
  font-size: 3vw;
  text-align: justify;
  line-height: 1.5;
}

.about p:last-child {
  margin-top: 3vw;
}

.gallery .section-title {
  margin-left: 5vw;
}

.gallery-wrapper {
  background-color: var(--color-primary);
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  gap: 10vw;
  padding: 10vw;
}

.gallery-item {
  position: relative;
  width: 50%;
}

.gallery-item-title {
  position: absolute;
  top: 10%;
  left: -50%;
  font-family: "Bai Jamjuree", sans-serif;
  font-size: 8vw;
  line-height: 1.2;
  text-transform: uppercase;
  color: var(--color-lighter);
  z-index: 1;
  mix-blend-mode: color-dodge;
}

.gallery-item-category {
  position: absolute;
  left: 0;
  bottom: -5%;
  text-transform: uppercase;
  color: var(--color-lighter);
  letter-spacing: 10px;
  z-index: 1;
}

.gallery-item-img {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
}

.footer {
  text-align: center;
}

.footer h1 {
  font-family: "Bodoni Moda", serif;
  font-size: 10vw;
  text-transform: lowercase;
  color: var(--color-primary);
}

.not-found {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-content: flex-start;
  column-gap: 5vw;
  row-gap: 1vw;
  padding: 5vw 0;
}
.headline-1,
.headline-2 {
  font-family: "Bai Jamjuree", sans-serif;
  font-size: 8vw;
  font-weight: 700;
  line-height: 1;
  text-transform: capitalize;
}
.img-1,
.img-2 {
  width: 20vw;
  height: 20vw;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}
.img-1 img,
.img-2 img {
  display: block;
  width: 100%;
}
.img-2 {
  justify-self: self-end;
}

.blog-wrapper {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(3, 1fr);
  height: 100vh;
  gap: 1rem;
}

.blog-photo {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  overflow: hidden;
  position: relative;
  transition: 2s;
}
.blog-content {
  position: relative;
}

.blog-content:nth-child(1) {
  grid-column: 1/3;
  grid-row: 1/3;
}
.blog-content:nth-child(2) {
  grid-column: 1/3;
  grid-row: 3;
}
.blog-content:nth-child(3) {
  grid-row: 1/4;
}
.blog-content:nth-child(4) {
  grid-column: 4/6;
}
.blog-content:nth-child(5) {
  grid-column: 4/6;
  grid-row: 2/4;
}
.blog-text,
.blog-three-text {
  position: absolute;
  z-index: 1;

  background-color: var(--color-primary);
  color: var(--color-lighter);
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  font-family: "Bai Jamjuree", sans-serif;
  text-align: center;
  transition: 1s;
}

.blog-text h4,
.blog-three-text h4 {
  font-size: 2vw;
}
.blog-text p,
.blog-three-text p {
  font-size: 1vw;
}

.blog-content:hover .blog-photo {
  filter: brightness(0.2);
}

.blog-content:hover .blog-text {
  transform: translate(0%, -50%);
  opacity: 1;
}
.blog-content:hover .blog-rotate-text {
  transform: rotate(90deg) translate(-100%, 0);
  transform-origin: bottom left 0;
  width: 271%;
  opacity: 1;
}
